<template>
    <!-- 返回顶部 -->
      <div class="go-top"
      :class="classList"
      v-if="isShow">
      <div class="top" @click="toGotop">
        <img :src="`${$cdnImageUrl}/common/common-to-top-icon.png`" />
        <span>顶部</span>
      </div>
      <!-- isHaierApp && -->
      <div class="divider" v-if="isHaierApp && feedShow"></div>
      <div class="bottom" v-if="isHaierApp && feedShow" @click="toFeedBack()">
        <img :src="`${$cdnImageUrl}/common/common-edit-icon.png`" />
        <span>反馈</span>
      </div>
      <!-- <div class="divider" v-if="serviceShow"></div>
      <div class="bottom service" v-if="serviceShow" @click="toService()">
        <img :src="`${$cdnImageUrl}/common/common-service-icon.png`" />
        <span>客服</span>
      </div> -->
    </div>
</template>

<script>
export default {
  props: {
    isShow: {
      type: Boolean,
      default: () => { return false }
    },
    // fromTitle: {
    //   type: String,
    //   default: () => { return '' }
    // },
    classList: {
      type: Object,
      default: () => { return {} }
    },
    feedShow: {
      type: Boolean,
      default: () => { return true }
    },
    // serviceShow: {
    //   type: Boolean,
    //   default: () => { return true }
    // },
    code: {
      type: Number,
      default: () => { return null }
    },
  },
  data() {
    return {
      isHaierApp: false, // 验证是否智家app内
      timer: null, // 计时器
    }
  },
  created() {
    this.isHaierApp = this.$util.isUplus()
  },
  watch: {
    isShow: {
      handler (newVal) {
        console.log(newVal)
        console.log('this.timer:', this.timer)
        if (newVal) {
          if (this.timer) {
            clearTimeout(this.timer)
            this.timer = null
          } else {
            this.timer = setTimeout(() => {
              this.hiddenFeedBack()
            }, 4000);
          }
        } else {
          clearTimeout(this.timer)
          this.timer = null
        }
      },
      // immediate: true,
    },
  },
  methods: {
    /**
     * 隐藏 FeedBack
     */
    hiddenFeedBack() {
      console.log('hiddenFeedBack')
      this.$emit('hiddenFeedBack')
    },
    /**
     * 返回顶部
     */
    toGotop() {
      this.$emit('toTop')
    },
    // /**
    //  * @description 跳平台客服
    // */
    // toService() {
    //   this.$router.push({ path: '/customerService',
    //     query: {
    //       accountId: 'zhijiashop',
    //       fromPageUrl: document.URL,
    //       fromPageTitle: this.fromTitle
    //     }
    //   })
    // },
    /**
     * 跳转反馈页
    */
    toFeedBack() {
      // 验证登录
      if (this.$store.getters.getLoginStatus) {
        let url = 'https://uplus.haier.com/uplusapp/problemFeedBack/feedback.html'
        if (this.code) {
          url = 'https://uplus.haier.com/uplusapp/problemFeedBack/feedback.html?code=' + this.code
        }
        this.$util.openNewWindow(url, true, false, false)
      } else {
        this.$store.dispatch('doLogin').then(res => {
          if (res === 400) {
            console.log('请求异常')
          }
        })
      }
    },
  }
}
</script>

